import React from 'react';
import { Ipokemon } from '../types/pokemon';
import { usePokemons } from '../hooks/poke_hooks'; // Hook criado
import styles from '../styles/Home.module.css';
import{ENDPOINTS}from '../endpoints';
const HomePage: React.FC = () => {
  const { data: pokemons, loading, error } = usePokemons(ENDPOINTS.USERS);

  const handleLike = (pokemon: Ipokemon) => {
    console.log(`Liked: ${pokemon.name}`);
    // Integre lógica de like aqui
  };

  const handleDislike = (pokemon: Ipokemon) => {
    console.log(`Disliked: ${pokemon.name}`);
    // Integre lógica de dislike aqui
  };

  if (loading) {
    return <div className={styles.container}>Carregando Pokémons...</div>;
  }

  if (error) {
    return (
      <div className={styles.container}>
        Ocorreu um erro ao carregar os Pokémons: {error}
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <h1>Pokémon List</h1>
      <div className={styles['pokemon-list']}>
        {pokemons.map((pokemon) => (
          <div key={pokemon.name} className={styles['pokemon-card']}>
            <img
              src={pokemon.image}
              alt={pokemon.name}
              className={styles['pokemon-image']}
            />
            <h3>{pokemon.name}</h3>
            <p>Category: {pokemon.category}</p>
            <p>Abilities: {pokemon.abilities.join(', ')}</p>
            <button onClick={() => handleLike(pokemon)}>Like</button>
            <button onClick={() => handleDislike(pokemon)}>Dislike</button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default HomePage;
