import { useState, useEffect } from 'react';
import { loadApi } from '../services/loadApi';
import { IpokeApiResponse } from '../types/poke_api_interface';

export const usePokemons = (endpoint: string) => {
  const [data, setData] = useState<IpokeApiResponse | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const load = async () => {
      setLoading(true); // Começa carregando
      try {
        const response = await loadApi(endpoint); // Chama o serviço
        console.log(response); // Log para depuração
        setData(response); // Define os dados no estado
      } catch (err) {
        console.error(err); // Log do erro
        setError('Erro ao carregar os dados'); // Define a mensagem de erro
      } finally {
        setLoading(false); // Finaliza o carregamento
      }
    };

    load();
  }, [endpoint]);

  return { data, loading, error };
};


